import React, { useContext, useEffect, useState } from 'react'
import { CartContext } from '../../Context/CartContext'
import { BallTriangle } from 'react-loader-spinner'
import { Link } from 'react-router-dom'
import { Helmet } from 'react-helmet';

export default function Cart() {

const [cartDetails,setCartDetails] = useState({})


let {getCart,deletProductFromCart,updateProductQuantity,setNumOfCartItems} = useContext(CartContext)


async function removeItem(id){
let {data} = await deletProductFromCart(id)
setNumOfCartItems(data?.numOfCartItems)
setCartDetails(data)
}

async function updatCount(id,count){
  let {data} = await updateProductQuantity(id,count)

  setCartDetails(data)
  }

async function getCartDetails(){
let {data} = await  getCart()
setNumOfCartItems(data?.numOfCartItems)
setCartDetails(data)
}

useEffect(()=>{
  getCartDetails()
},[])


  return (
    <>

<Helmet>
                <title>Cart</title>
             
            </Helmet>

    {cartDetails?.data ?    <div className="container my-5">
        <div className=" mx-auto bg-main-light mb-5  p-5">
          <h1 className='mb-4'>Cart shop</h1>
          <div className="d-flex justify-content-between align-items-center">
            <h3>Total price : <span className='text-main'>{cartDetails?.data.totalCartPrice} EGP</span></h3>
            <h3>Total Cart Items : <span className='text-main'>{cartDetails?.numOfCartItems}</span></h3>
          </div>
          {cartDetails?.data.products.map((ele)=> <div key={ele.product._id} className="row mb-5 py-3 border-bottom">
          <div className="col-md-1">
            <img src={ele.product.imageCover} className='w-100' alt="" />
          </div>
        

          <div className="col-md-11">
            <div className="d-flex justify-content-between ">
             <div className="left-side">
              <h4>{ele.product.title}</h4>
              <p>{ele.price} EGP</p>
             </div>
            
             <div className="right-side">
              <button onClick={()=> updatCount(ele.product._id, ele.count -1)} className='btn border-success'>-</button>
              <span className='mx-3'>{ele.count}</span>
              <button onClick={()=> updatCount(ele.product._id, ele.count +1)} className='btn border-success'>+</button>
             </div>
            
            </div>
            <button onClick={()=> removeItem(ele.product._id)} className='btn p-0 text-danger'><i className='fa fa-trash-can'></i> remove</button>
          </div>
        
        </div>)}
        <Link className='btn text-white bg-main w-100 mt-5' to={"/CheckOut"}>Checkout</Link>
        </div>
        
      </div>:<BallTriangle
  height={100}
  width={100}
  radius={5}
  color="#4fa94d"
  ariaLabel="ball-triangle-loading"
  wrapperStyle={{}}
  wrapperClass={"justify-content-center"}
  visible={true}
  />}
   
    </>
  )
}


