import axios from "axios";
import { createContext, useEffect, useState } from "react";

let  headers = {
    token: localStorage.getItem("userToken")
  }  



export let CartContext = createContext();



function addToCart(id){
return axios.post(`https://ecommerce.routemisr.com/api/v1/cart`,{
    productId: id
},
{
    headers
}).then((res)=>res).catch((err)=>err)
}

function getCart(){
    return axios.get(`https://ecommerce.routemisr.com/api/v1/cart`,
    {
        headers
    }).then((res)=>res).catch((err)=>err)
    }

    function deletProductFromCart(id){
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${id}`,
        {
            headers
        }).then((res)=>res).catch((err)=>err)
        }


        function updateProductQuantity(id,count){
            return axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${id}`,
            {
                count
            },
            {
                headers
            }).then((res)=>res).catch((err)=>err)
            }
     

export default function CartContextProvider(props){

    function onlinePyament(shippingAddress){
        return axios.post(`https://route-ecommerce.onrender.com/api/v1/orders/checkout-session/${cartId}?url=http://localhost:3000`,
        {
            shippingAddress
        },
        {
            headers
        }).then((res)=>res).catch((err)=>err)
    }
    



    const [cartId,setCartId]= useState(null)
    const [numOfCartItems,setNumOfCartItems]= useState(null)

async function getInitialCart(){
 let {data} =  await getCart()
 setNumOfCartItems(data?.numOfCartItems)
 setCartId(data?.data._id)
}

useEffect(()=>{
    getInitialCart()
},[])


    return <CartContext.Provider value={{numOfCartItems,setNumOfCartItems,onlinePyament,addToCart,getCart,deletProductFromCart,updateProductQuantity}}>

    {props.children}

    </CartContext.Provider>
}