import React, {  useContext, useState } from 'react'
import styles from './Login.module.css'
import { Link, useNavigate } from 'react-router-dom';
import * as yup from 'yup'
import axios from 'axios';
import { useFormik } from 'formik';
import { TokenContext } from './../../Context/Token';
import { Helmet } from 'react-helmet';

export default function Login() {
  let navigate =  useNavigate()
  const [errorMessage,setErrorMessage]=useState("")
  const [isLoading,setisLoading]=useState(false)

let {setToken} = useContext(TokenContext)

    async function callLogin(reqBody){


      setErrorMessage("")
      setisLoading(true)
   let {data} = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signin',reqBody)
   .catch(err => {
    setisLoading(false)
    setErrorMessage(err.response.data.message)
   } )
  
   if(data.message == 'success'){
    localStorage.setItem("userToken",data.token)
    setToken(data.token)
    
  navigate("/Home")
   }
    }
  
  const validationSchema = yup.object({
  
    email: yup.string().email("email is invalid").required("email is required"),
    password :yup.string().matches().required("password is required"),
   
   
  })
  
    const loginForm = useFormik({
      initialValues:{
     
      email:"",
      password:"",
  
      },
  
      validationSchema,
      onSubmit: callLogin
    })
    return (
      <>
<Helmet>
                <title>login page</title>
             
            </Helmet>

  <div className="w-50 mx-auto">
  <h2 className='mb-3'>Login Now</h2>
  {errorMessage ? <div className='alert alert-danger'>{errorMessage}</div>:null}
  
  <form  onSubmit={loginForm.handleSubmit}>
 
    <div className="form-group mb-3">
    <label htmlFor="Email"className='mb3'>Email</label>
    <input 
    type="email" 
    id='Email'
    value={loginForm.values.email}
     name='email'
    onChange={loginForm.handleChange}
     className='form-control'
     onBlur={loginForm.handleBlur}
     />
      {loginForm.errors.email && loginForm.touched.email ? (
      <div className='alert alert-danger'>
        {loginForm.errors.email}
        </div>
        ):null}
    </div>
    <div className="form-group mb-3">
    <label htmlFor="Password"className='mb3'>Password</label>
    <input
     type="password"
      id='Password'
      value={loginForm.values.password} 
      name='password'onChange={loginForm.handleChange} 
      className='form-control'
      onBlur={loginForm.handleBlur}
      />
       {loginForm.errors.password && loginForm.touched.password ? (
       <div className='alert alert-danger'>
        {loginForm.errors.password}
        </div>
        ):null}
    </div>
   
    <Link className="d-block mb-3 forget" to={"/ForgetPass"}>
            Forget password ?
          </Link>
    
    <button type='submit'
    
     className='btn bg-main text-white'>
      {isLoading ?<i className='fa fa-spin fa-spinner'></i>:"Login"}
      
      
      </button>
  </form>
  </div>
      </>
    );
}
