import React, { useContext } from 'react'
import styles from './ProductDetails.module.css'
import { useParams } from 'react-router-dom';
import { useQuery } from 'react-query';
import axios from 'axios';
import { BallTriangle } from 'react-loader-spinner';
import { Helmet } from 'react-helmet';
import Slider from 'react-slick';
import { CartContext } from '../../Context/CartContext';
import toast from 'react-hot-toast';


export default function ProductDetails() {
  let settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };

  let {addToCart,setNumOfCartItems} = useContext(CartContext)

 let params = useParams()
 function getProductDetais(id){
  return axios.get(`https://route-ecommerce.onrender.com/api/v1/products/${id}`)
 }


 async function addCart(id){
  let res = await addToCart(id)
  if(res.data.status == "success"){
    toast.success('product added successfully');
    setNumOfCartItems(res.data.numOfCartItems)

  }else{
    toast.error('product does not added');
  }
  }





 let {data,isLoading} = useQuery("details",()=>getProductDetais(params.id))

  return (
   
    <>
<Helmet>
                <title>product Details page</title>
             
            </Helmet>

<div className="container my-5">
  {isLoading ? <BallTriangle
  height={100}
  width={100}
  radius={5}
  color="#4fa94d"
  ariaLabel="ball-triangle-loading"
  wrapperStyle={{}}
  wrapperClass={"justify-content-center"}
  visible={true}
  /> :<div className="row align-items-center">
<div className="col-md-4">
<Slider {...settings}>

{data?.data?.data.images.map((ele,index)=> <img key={index} src={ele}alt='' />)}

</Slider>
</div>
<div className="col-md-8">
  <h2>{data?.data?.data.title}</h2>
  <p>{data?.data?.data.description}</p>
  <p>{data?.data?.data.category.name}</p>
  <div className="d-flex justify-content-between">
    <h5>{data?.data?.data.price}</h5>
    <h5> <i className='fa fa-star rating-color'></i>{data?.data?.data.ratingsAverage}</h5>
  </div>
  <button onClick={()=> addCart(data?.data?.data.id)} className='btn bg-main text-white w-100'>add to cart</button>
</div>

</div> }



</div>
    </>
  )
}
