import { useFormik } from 'formik'
import React, { useContext } from 'react'
import { Helmet } from 'react-helmet';
import { CartContext } from '../../Context/CartContext';
import * as yup from "yup";

export default function CheckOut() {
  let {onlinePyament} = useContext(CartContext)

async function payment(values){
let {data} = await onlinePyament(values)
window.location.href = data.session.url;
}

let validationSchema = yup.object({
  details: yup.string().required("details is required"),
  phone: yup
    .string()
    .required("phone is required")
    .matches(/^01[1250][0-9]{8}$/, "enter valid phone"),
  city: yup.string().required("city is required"),
  
});


  let formik = useFormik({
initialValues:{
  "details":"",
  "phone":"",
  "city":"",
},
validationSchema,
onSubmit: payment
  })


  return (
    <>
<Helmet>
                <title>Checkout</title>
             
            </Helmet>


    <div className="container">
      <div className='mx-auto bg-main-light p-5'>
       <h2>shipping adress</h2>
       <form onSubmit={formik.handleSubmit}>
        <div className="form-group mb-3">
          <label htmlFor="details">Details</label>
          <input
           type="text"
            className='form-control'
             id='details' 
             name='details'
             value={formik.values.details}
             onChange={formik.handleChange}
              />
                  {formik.errors.details && formik.touched.details ? (
              <div className="text-danger">{formik.errors.details}</div>
            ) : (
              ""
            )}
        </div>

        <div className="form-group mb-3">
          <label htmlFor="phone">Phone</label>
          <input
           type="tel"
            className='form-control'
             id='phone' 
             name='phone'
             value={formik.values.phone}
             onChange={formik.handleChange}
              />
                 {formik.errors.phone && formik.touched.phone ? (
              <div className="text-danger">{formik.errors.phone}</div>
            ) : (
              ""
            )}
        </div>

        <div className="form-group mb-3">
          <label htmlFor="city">City</label>
          <input
           type="text"
            className='form-control'
             id='city' 
             name='city'
             value={formik.values.city}
             onChange={formik.handleChange}
              />
                   {formik.errors.city && formik.touched.city ? (
              <div className="text-danger">{formik.errors.city}</div>
            ) : (
              ""
            )}
        </div>
        <button type='submit' className='btn bg-main text-white my-3 w-100'>pay now</button>
       </form>
      </div>
    </div>
      
    </>
  )
}
