import React, { useEffect, useState } from 'react'
import styles from './Allorders.module.css'
import { jwtDecode } from 'jwt-decode';
import axios from 'axios';
export default function Allorders() {

  let [myData, setMyData] = useState([]);


  let decoded = jwtDecode(localStorage.getItem("userToken"));
  async function getAllorders(id) {

    let req = await axios
      .get(`https://route-ecommerce.onrender.com/api/v1/orders/user/${id}`)
      .catch((err) => {
        console.log(err);
      });
    if (req?.data.status === "success") {
      setMyData(req?.data.data);
      console.log(req.data.data);
    }
    
  }

  useEffect(() => {
    getAllorders(decoded.id);
    window.scrollTo(0, 0);
  }, []);



  return (
    <>
<div className="container">
  <div className="bg-success my-5 text-center text-white p-5">
  <h2>congartulations</h2>
  </div>
</div>

  <div>
            <div className="all my-5">
              <h1>All order..</h1>
            </div>
            <div className="cards bg-dark">
              {myData.map((el) => {
                return (
                  <div className="card">
                    <img className="w-100" src={el.imageCover} alt="" />
                  </div>
                );
              })}
            </div>
          </div>


    </>
  )
}
