import React, { useState } from 'react'
import styles from './Register.module.css'
import { useFormik } from 'formik'
import * as yup from "yup"
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import { Helmet } from 'react-helmet'
export default function Register() {
  
let navigate =  useNavigate()
const [errorMessage,setErrorMessage]=useState("")
const [isLoading,setisLoading]=useState(false)
  async function callRegister(reqBody){
    setErrorMessage("")
    setisLoading(true)
 let {data} = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signup',reqBody)
 .catch(err => {
  setisLoading(false)
  setErrorMessage(err.response.data.message)
 } )
 console.log(data)
 if(data.message == 'success'){
navigate("/Login")
 }
  }

const validationSchema = yup.object({
  name: yup.string().min(3,"name is too short").max(30,"name is too long").required("name is required"),
  email: yup.string().email("email is invalid").required("email is required"),
  password :yup.string().matches(/^[A-Z][a-zA-Z!@#$%^&*()_]{6,16}$/, "enter valid passowrd").required("password is required"),
  rePassword :yup.string().oneOf([yup.ref("password")],"password and repassword dont match").required("repasword is required"),
  phone: yup.string().matches(/^01[0125][0-9]{8}$/,"phone is invalid").required("phone required")
})

  const registerForm = useFormik({
    initialValues:{
    name:"",
    email:"",
    password:"",
    rePassword:"",
    phone:""
    },

    validationSchema,
    onSubmit: callRegister
  })
  return (
    <>
<Helmet>
                <title>register page</title>
             
            </Helmet>


<div className="w-50 mx-auto">
<h2 className='mb-3'>Register Now</h2>
{errorMessage ? <div className='alert alert-danger'>{errorMessage}</div>:null}

<form  onSubmit={registerForm.handleSubmit}>
  <div className="form-group mb-3">
  <label htmlFor="fullName"className='mb3'>Full Name</label>
  <input
   type="text"
   id='fullName'
   value={registerForm.values.name} 
   name='name'
   onChange={registerForm.handleChange} 
   className='form-control'
   onBlur={registerForm.handleBlur}

   />
   {registerForm.errors.name && registerForm.touched.name ?(
     <div className='alert alert-danger'>
      {registerForm.errors.name}
      </div>
      ):null}
  </div>
  <div className="form-group mb-3">
  <label htmlFor="Email"className='mb3'>Email</label>
  <input 
  type="email" 
  id='Email'
  value={registerForm.values.email}
   name='email'
  onChange={registerForm.handleChange}
   className='form-control'
   onBlur={registerForm.handleBlur}
   />
    {registerForm.errors.email && registerForm.touched.email ? (
    <div className='alert alert-danger'>
      {registerForm.errors.email}
      </div>
      ):null}
  </div>
  <div className="form-group mb-3">
  <label htmlFor="Password"className='mb3'>Password</label>
  <input
   type="password"
    id='Password'
    value={registerForm.values.password} 
    name='password'onChange={registerForm.handleChange} 
    className='form-control'
    onBlur={registerForm.handleBlur}
    />
     {registerForm.errors.password && registerForm.touched.password ? (
     <div className='alert alert-danger'>
      {registerForm.errors.password}
      </div>
      ):null}
  </div>
  <div className="form-group mb-3">
  <label htmlFor="rePassword"className='mb3'>rePassword</label>
  <input
   type="password"
    id='rePassword'
    value={registerForm.values.rePassword} 
    name='rePassword'
    onChange={registerForm.handleChange} 
    className='form-control'
    onBlur={registerForm.handleBlur}

    />
     {registerForm.errors.rePassword && registerForm.touched.rePassword ? (
     <div className='alert alert-danger'>
      {registerForm.errors.rePassword}
      </div>
      ):null}
  </div>
  <div className="form-group mb-3">
  <label htmlFor="phone"className='mb3'>phone</label>
  <input
   type="tel"
    id='phone'
    value={registerForm.values.phone} 
    name='phone'
    onChange={registerForm.handleChange} 
    className='form-control'
    onBlur={registerForm.handleBlur}
    />
    {registerForm.errors.phone && registerForm.touched.phone ?(
       <div className='alert alert-danger'>

        {registerForm.errors.phone}
        </div>
        ):null}
  </div>
  
  <button type='submit'
  disabled={!(registerForm.isValid && registerForm.dirty )}
   className='btn bg-main text-white'>
    {isLoading ?<i className='fa fa-spin fa-spinner'></i>:"Register"}
    
    
    </button>
</form>
</div>
    </>
  );
}
