import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { BallTriangle } from 'react-loader-spinner'
import { useQuery } from 'react-query'
import { Link } from 'react-router-dom';
import { CartContext } from '../../Context/CartContext';
import toast from 'react-hot-toast';


export default function FeatureProducts() {

let {addToCart,setNumOfCartItems} = useContext(CartContext)

function getProducts(){
return  axios.get("https://ecommerce.routemisr.com/api/v1/products")
}


let {isLoading,data,isError} = useQuery("featuredProducts",getProducts)

async function addCart(id){
let res = await addToCart(id)
if(res.data.status == "success"){
  toast.success('product added successfully');
  setNumOfCartItems(res.data.numOfCartItems)
}else{
  toast.error('product does not added');
}
}

  return (
    <>
      <div className="container py-5">

        {isLoading ? <BallTriangle
  height={100}
  width={100}
  radius={5}
  color="#4fa94d"
  ariaLabel="ball-triangle-loading"
  wrapperStyle={{}}
  wrapperClass={"justify-content-center"}
  visible={true}
  />:<div className="row">
  {data?.data?.data.map((ele)=> <div key={ele.id} className="col-md-2">
  <div className="product px-2 py-3">
    <Link to={"ProductDetails/"+ ele.id}>
    <img src={ele.imageCover}className='w-100' alt={ele.title} />
<p className='text-main'>{ele.category.name}</p>
<h3 className='h6'>{ele.title.split(" ").slice(0,3).join(' ')}</h3>
<div className='d-flex justify-content-between'>
<p>{ele.price} EGP</p>
<p><i className='fa fa-star rating-color'></i>
{ele.ratingsAverage}
</p>
</div>
    </Link>

<button onClick={()=> addCart(ele.id)} className='bg-main btn text-white w-100'>add to cart</button>


  </div>
</div>)}

</div>}




      </div>
    </>
  )
}
